<template>
    <div class="auth-container">
        <h2>–í—Ö–æ–¥</h2>
        <form @submit.prevent="handleLogin">
        <div>
            <label for="username">–ò–º—è –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è:</label>
            <input type="text" id="username" v-model="username" required />
        </div>
        <div>
            <label for="password">–ü–∞—Ä–æ–ª—å:</label>
            <input type="password" id="password" v-model="password" required />
        </div>
        <div>
            <button type="submit">–í–æ–π—Ç–∏</button>
        </div>
        <p v-if="error" class="error">{{ error }}</p>
        </form>
        <p>–ù–µ—Ç –∞–∫–∫–∞—É–Ω—Ç–∞? <router-link to="/register">–ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è</router-link></p>
    </div>
</template>

<script>
import axios from 'axios';  // –ü–æ–¥–∫–ª—é—á–∞–µ–º axios

export default {
    data() {
        return {
        username: '',
        password: '',
        error: null,
        };
    },
    methods: {
        async handleLogin() {
        this.error = null;
        try {
            // –û—Ç–ø—Ä–∞–≤–ª—è–µ–º POST-–∑–∞–ø—Ä–æ—Å –Ω–∞ —Å–µ—Ä–≤–µ—Ä –¥–ª—è –ª–æ–≥–∏–Ω–∞
            const response = await axios.post('http://127.0.0.1:8000/api/login/', {
            username: this.username,
            password: this.password,
            });

            // –ï—Å–ª–∏ –≤—Å–µ —É—Å–ø–µ—à–Ω–æ, —Å–æ—Ö—Ä–∞–Ω—è–µ–º —Ç–æ–∫–µ–Ω –∏ —Ä–µ–¥–∏—Ä–µ–∫—Ç–∏–º
            console.log('Logged in:', response.data);
            localStorage.setItem('access_token', response.data.access); // –°–æ—Ö—Ä–∞–Ω—è–µ–º —Ç–æ–∫–µ–Ω
            this.$router.push('/');  // –ü–µ—Ä–µ–Ω–∞–ø—Ä–∞–≤–ª—è–µ–º –Ω–∞ –¥–æ–º–∞—à–Ω—é—é —Å—Ç—Ä–∞–Ω–∏—Ü—É
        } catch (error) {
            // –ï—Å–ª–∏ –æ—à–∏–±–∫–∞, –≤—ã–≤–æ–¥–∏–º —Å–æ–æ–±—â–µ–Ω–∏–µ
            this.error = error.response?.data?.detail || '–ù–µ–≤–µ—Ä–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ –¥–ª—è –≤—Ö–æ–¥–∞';
        }
        },
    },
};
</script>

<style scoped>
.auth-container {
    max-width: 400px;
    margin: 0 auto;
    padding: 1rem;
    border: 1px solid #ccc;
    border-radius: 8px;
    background-color: #f9f9f9;
}

h2 {
    text-align: center;
}

input {
    width: 100%;
    padding: 8px;
    margin: 8px 0;
    border-radius: 4px;
    border: 1px solid #ccc;
}

button {
    width: 100%;
    padding: 10px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 4px;
}

button:hover {
    background-color: #45a049;
}

.error {
    color: red;
    font-size: 14px;
}
</style>
