
<template>
    <div class="Arrow_Back">
    <!-- –û—Å–Ω–æ–≤–Ω–∞—è —Ä–∞–∑–º–µ—Ç–∫–∞ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞ -->
        <button class="Arrow_Back_btn" @click="handleClick">
            <img src="../assets/make_training/Arrow.png">
        </button>
    </div>
</template>


<script>
export default {
  name: 'ArrowBack', // –ù–∞–∑–≤–∞–Ω–∏–µ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞

    props: {
        action: {
            type: String,
            default: 'closeModal'
        }
    },

    data() {
    return {
      // –õ–æ–∫–∞–ª—å–Ω–æ–µ —Å–æ—Å—Ç–æ—è–Ω–∏–µ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞
    };
    },

    methods: {
        handleClick() {
            if (this.action === 'goHome') {
                this.$router.push('/');
            } else if (this.action === 'goChooseTraining') {
                this.$router.push('/choose_training');
            } else {
                this.$emit('close');
            }
        }
    },
};
</script>

<style scoped>


.Arrow_Back button {
    color: #fff;
    opacity: 20%;
    border-radius: 24px;
    width: 100px;
    height: 100px;
}

.Arrow_Back_btn img{
    opacity: 100%;
}

    
</style>
